* {
  margin: 0;
  padding: 0;
}

.screen {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: rgb(16, 64, 16);
}

.scan {
  position: absolute;
  opacity: 40%;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.content {
  font-family: VT323;
  padding: 50px;
  color: rgb(150, 255, 0);
  font-size: 20px;
  text-shadow: 1px 1px 2px rgb(150, 255, 100), 0 0 1em rgb(150, 255, 100),
    0 0 0.25em rgb(150, 255, 100);
}

input {
  width: calc(100% - 2rem);
  border: none;
  outline: none;
  font-size: 16px;
  text-decoration: none;
  background-color: transparent;
  color: rgb(150, 255, 0);
  text-shadow: 1px 1px 1px rgb(150, 255, 100), 0 0 0.5em rgb(150, 255, 100),
    0 0 0.125em rgb(150, 255, 100);
}

.terminalResults {
  padding-left: 1rem;
  max-height: 63vh;
  white-space: pre-line;
  overflow: auto;
}
